<template>
  <div :class="modalClass">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title" v-if="info"><b>{{ info.ITEM_NAME }}</b>: Calc</p>
        <button class="delete" aria-label="close" @click="$emit('hide-modal')"></button>
      </header>
      <section class="modal-card-body">
        <div v-if="type == 'nutrition'"></div>
        <div class="container">
          <div class="columns">
            <label class="label column is-2">Weight:</label>
            <input type="number" class="column is-2 input is-info" v-model="weight"/>
          </div>

          <div class="columns is-mobile">
            <label class="is-4-mobile is-3-desktop is-3-tablet column mt-3" for="concSlider">Concentration</label>
            <div class="column is-8">
              <input id="concSlider" v-model="concentration" class="slider has-output" 
              step="0.1" min="1.0" max="1.5" type="range">
              <output for="concSlider">{{ concentration }} </output>
            </div>
          </div>

          <div class="field">
            <label class="mr-2">Bolus</label>
            <input :id="intakeId" type="checkbox" :name="intakeId" class="switch" v-model="intakeMode">
            <label :for="intakeId">Drip</label>
          </div>

          <div v-if="intakeMode" class="columns">
            <label class="is-2 column mt-3" for="dripSlider">Rate(ml/hr)</label>
            <div class="column is-6">
              <input id="dripSlider" v-model="dripValue" class="slider has-output" 
              step="5" min="5" max="150" type="range">
              <output for="dripSlider">{{ dripValue }} </output>
            </div>
          </div>

          <div v-if="!intakeMode" class="columns is-mobile">
            <label class="is-2 column mt-3" for="feedSlider">Feeds</label>
            <div class="column is-8">
              <input id="feedSlider" v-model="feedValue" class="slider has-output" 
              step="1" min="1" max="8" type="range">
              <output for="feedSlider">{{ feedValue }} </output>
            </div>
          </div>
          <div v-if="!intakeMode" class="columns is-mobile">
            <label class="is-2 column mt-3" for="feedVolSlider">ml</label>
            <div class="column is-8">
              <input id="feedVolSlider" v-model="feedVolume" class="slider has-output" 
              step="10" min="50" max="500" type="range">
              <output for="feedVolSlider">{{ feedVolume }} </output>
            </div>
          </div>

        </div>
        <hr>
        <div class="columns is-mobile">
          <span class="column is-3"><strong>Total Volume</strong></span>
          <span class="column is-4">{{ totalVolume }} ml</span>
          <small class="column is-5 has-text-grey">Holliday-Segar: {{ holliday }} ml</small>
        </div>
        <div class="columns is-mobile">
          <span class="column is-3"><strong>Total Calories</strong></span>
          <span class="column is-4">{{ totalCal }} kCal</span>
          <span class="column is-5 has-text-grey">{{ (totalCal/weight).toFixed(2) }} kCal/kg</span>
        </div>
        <div class="columns is-mobile">
          <span class="column is-3"><strong>Total Protein</strong></span>
          <span class="column is-4">{{ totalProt.toFixed(0) }} gm</span>
          <span class="column is-5 has-text-grey">{{ (totalProt/weight).toFixed(2) }} gm/kg</span>
        </div>

      </section>
      <footer class="modal-card-foot">
        <button class="button" @click="$emit('hide-modal')">Close</button>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "InfoModal",
  props: ['type', 'info', 'activated', 'hideModal'],
  data: function(){
    return {
      isActivate: false,
      weight: 60,
      intakeMode: true,
      dripValue: 40,
      feedValue: 4,
      feedVolume: 300,
      concentration: 1.0,
    }
  },
  mounted: function(){
    this.isActivate = this.activated || false
  },
  computed: {
    modalClass: function(){
      return {
        modal: true,
        'is-active': this.activated,
      }
    },
    totalVolume: function(){
      if(this.intakeMode) return this.dripValue*24;
      else return this.feedValue*this.feedVolume;
    },
    holliday: function(){
      let wt = this.weight;
      if(wt <= 10) return wt*100; 
      if(wt <= 20) return 1000+(wt-10)*50;
      return 1500+(wt-20)*20;
    },
    totalCal: function(){
      return this.totalVolume*this.concentration;
    },
    totalProt: function(){
      return this.totalCal/1000*this.info.PROT;
    },
    intakeId: function(){
      return "intakeMode"+this.info.ID;
    }
  },
  methods: {
    formatTitle: function(msg){
      msg = msg.charAt(0).toUpperCase() + msg.slice(1);
      return msg
    },
  }
}

</script>

<style scoped>
.switch[type="checkbox"] + label::before {
  background: #00d1b2;
}
</style>
