<template>
  <div class="box column is-half is-offset-one-quarter">
    {{ data.ITEM_NAME }}
    <span v-if="type == 'nutrition'" class="m-1">
      <button class="is-small mr-2 button is-link is-rounded is-light"
              @click="showModal">Info</button>
      <button class="is-small button is-danger is-rounded is-light"
              @click="showCalc">Calc</button>
    </span>
    <Badge :type="type"/>
    <InfoModal v-if="type == 'nutrition'" 
               :activated="activated" 
               @hide-modal="hideModal"
               :type="type"
               :info="data"/>
    <NutritionCalcModal v-if="type == 'nutrition'" 
               :activated="calcActivated" 
               @hide-modal="hideModal"
               :type="type"
               :info="data"/>
  </div>
</template>

<script>
import Badge from './Badge.vue'
import InfoModal from './InfoModal.vue'
import NutritionCalcModal from './NutritionCalcModal.vue'

export default {
  name: "ResultItem",
  components: {
    Badge,
    InfoModal,
    NutritionCalcModal,
  },
  props: ["type", "data", "id"],
  data: function(){
    return {
      activated: false,
      calcActivated: false,
    }
  },
  methods: {
    hideModal: function(){
      this.activated = false
      this.calcActivated = false
    },
    showModal: function(){
      this.activated = true
    },
    showCalc: function(){
      this.calcActivated = true
    },
  }
}

</script>

