<template>
  <div>
    <div v-for="item in nameList" :key="item.id" class='columns block m-2'>
      <ResultItem :type="item.type" :data="item.data" :id="item.id"/>
    </div>
    <div v-if="nameList.length == 0" class="columns block m-2">
      <i class="column is-half is-offset-one-quarter">No result found</i>
    </div>
  </div>
</template>

<script>
import ResultItem from './ResultItem.vue'

export default {
  name: "ResultList",
  props: ['resultList'],
  components: {
    ResultItem,
  },
  data: function(){
    return {
    }
  },
  methods: {
  },
  computed: {
    nameList: function(){
      let list = []
      for(let id in this.resultList){
        list.push(this.resultList[id])
      }
      return list
    }
  },
}
</script>

